:root {
  --link-color: #00acdc;
  --header-links-height: 60px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  line-height: 1px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link:link {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  height: var(--header-links-height);
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-links-height);
  text-decoration: none;
  display: block;
}

.nav__link:visited {
  color: #000;
}

.nav__link:hover {
  color: var(--link-color);
}

.nav__link.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

